import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import MainSlider from "../component/MainSlider";
import get from "lodash/get";
import MainSectionOne from "../component/MainSectionOne";
import MainSectionTwo from "../component/MainSectionTwo";
import MainSectionThree from "../component/MainSectionThree";
import Books from "../component/Books";
import MainSectionFour from "../component/MainSectionFour";
import MainBottomSection from "../component/MainBottomSection";
import {Seo} from "../component/Seo";

@inject('ui', 'main', 'books', 'photos', 'pages')
@observer
export default class AppPage extends Component {
    toggleBanner = () => {
        this.props.ui.toggleBanner();
    };

    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.pages.getById(154);
        this.props.main.getAll();
        this.props.photos.getMainPhotos();
        this.props.books.getAll();
    }

    render() {
        const {ui: {options}, main: {main, isLoaded}, photos: {photos}, books: {books}, pages: {page}} = this.props;
        let mainSlider;
        let nextVebinar;
        let events;
        let rightSlider;
        let quote;
        let mainLaytmanLeft;
        let mainLaytmanRight;
        let intProjects;
        let intProjectsNums;
        let mainTeachers;
        let totalTeachers;
        let mainTraining;
        let mainNumbers;
        if (main.length > 0) {
            mainSlider = get(main, '[0].mainSlider[0]');
            nextVebinar = get(main, '[0].nextVebinar[0]');
            events = get(main, '[0].events');
            rightSlider = get(main, '[0].rightSlider');
            quote = get(main, '[0].quote[0]');
            mainLaytmanLeft = get(main, '[0].mainLaytman[0].block[0]');
            mainLaytmanRight = get(main, '[0].mainLaytman[0].list');
            intProjects = get(main, '[0].intProjects');
            intProjectsNums = get(main, '[0].intProjectsNums');
            mainTeachers = get(main, '[0].mainTeachers');
            totalTeachers = get(main, '[0].totalTeachers');
            mainTraining = get(main, '[0].mainTraining');
            mainNumbers = get(main, '[0].mainNumbers');
        }
        return (
            <Layout black={false} isMainPage={true}>
                <Seo page={page}/>
                <MainSlider mainSlider={mainSlider} isLoaded={isLoaded}/>
                <MainSectionOne
                    nextVebinar={nextVebinar}
                    options={options}
                    events={events}
                    rightSlider={rightSlider}
                    quote={quote}
                />
                <MainSectionTwo
                    mainLaytmanLeft={mainLaytmanLeft}
                    mainLaytmanRight={mainLaytmanRight}
                    photos={photos}
                />
                <MainSectionThree
                    events={events}
                    isLoaded={isLoaded}
                    intProjects={intProjects}
                    intProjectsNums={intProjectsNums}
                    mainTeachers={mainTeachers}
                    totalTeachers={totalTeachers}
                />
                <Books isMainPage={true} books={books}/>
                <MainSectionFour
                    mainTraining={mainTraining}
                />
                <MainBottomSection mainNumbers={mainNumbers}/>
            </Layout>
        );
    }
}