import React from "react";
import {Helmet} from "react-helmet";

export const Seo = ({page, catTitle, currentPage}) => {
    const seoTitle = page['yoast_title'];
    const seoMeta = page['yoast_meta'];
    return (
        Object.keys(page).length && (
            <Helmet>
                {
                    catTitle ? (
                        <title>{catTitle} - Каббала</title>
                    ) : (
                        <title>{seoTitle}</title>
                    )
                }
                {
                    currentPage !== '' && currentPage && (
                        <title>{seoTitle}{` - Страница ${currentPage}`}</title>
                    )
                }
                {seoMeta.map((item, index) => (
                    item.property !== 'og:url' && (
                        index === 0 || index === 1 ? (
                            <meta key={index} name={item.name} content={item.content}/>
                        ) : (
                            <meta key={index} property={item.property} content={item.content}/>
                        )
                    )
                ))}
                {/*<meta name={seoMeta[0].name} content={seoMeta[0].content} />*/}
                {/*<meta property={seoMeta[2].property} content={seoMeta[2].content} />*/}
                {/*<meta property={seoMeta[3].property} content={seoMeta[3].content} />*/}
                {/*<meta property={seoMeta[4].property} content={seoMeta[4].content} />*/}
                {/*<meta property={seoMeta[5].property} content={seoMeta[5].content} />*/}
                {/*<meta property={seoMeta[8].property} content={seoMeta[8].content} />*/}
                {/*<meta property={seoMeta[9].property} content={seoMeta[9].content} />*/}
                {/*<meta property={seoMeta[10].property} content={seoMeta[10].content} />*/}
                
            </Helmet>
        )
    )
};