import React from 'react';
import {observer} from "mobx-react";
import Scrollbar from "react-scrollbars-custom";
import Loader from "./Loader";
import {splitString} from "./SplitString";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Books = ({books, isLoaded, isMainPage}) => {
    const scrollInner = (children) => {
        return (
            <Scrollbar
                style={{width: "100%", height: 285}}
                key={Math.random()}
            >
                {children}
            </Scrollbar>
        )
    };
    const renderNewBooks = item => {
        return (
            <div className={'books-page__newbooks'} key={Math.random()}>
                <div className="books-page__newbooks--left">
                    <p>
                        {
                            isMainPage ? 'Kbooks.ru' : 'Новинки'
                        }
                    </p>
                </div>
                {
                    item.newBooks.map((book, index) => (
                        <div className="books-page__newbooks--item"
                             key={index}>
                            <a href={book.url} rel="noopener noreferrer" target='_blank' className="books-page__newbooks--img">
                                <LazyLoadImage
                                    alt={book.title}
                                    effect="blur"
                                    src={book.img}
                                />
                            </a>
                            <a rel="noopener noreferrer" target='_blank'
                               href={book.url}>{book.title}</a>
                        </div>
                    ))
                }
                {
                    isMainPage && (
                        <div className={'books-page__newbooks--right'}>
                            <a rel="noopener noreferrer" target='_blank' href="/#/books/">
                                Смотреть все
                            </a>
                        </div>
                    )
                }
            </div>
        )
    };
    const renderInner = () => {
        return (
            books && books.map(item => (
                <div className={'books-page__content'} key={item.title + Math.random()}>
                    {
                        item.newBooks && (
                            window.innerWidth <= 769 ?
                                scrollInner(renderNewBooks(item))
                                :
                                renderNewBooks(item)
                        )
                    }
                    {
                        item.blocks && (
                            <div className="books-page__columns">
                                {
                                    item.blocks.map(item => (
                                        <div className="books-page__columns--item" key={item.title + Math.random()}>
                                            <div className="books-page__columns--site">
                                                <a rel="noopener noreferrer" target='_blank' className="clrs-under" href={item.url} dangerouslySetInnerHTML={{__html: splitString(item.title)}}/>
                                            </div>
                                            <div className="books-page__columns--name">
                                                <p>{item.name}</p>
                                            </div>
                                            <div className="books-page__columns--description">
                                                <p>
                                                    {item.description}
                                                </p>
                                            </div>
                                            <div className="books-page__columns--link">
                                                <a rel="noopener noreferrer" target='_blank' href={item.url}>В магазин <svg width="15" height="18"
                                                                                  viewBox="0 0 15 18" fill="none"
                                                                                  xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M1 1.76619L13.0563 9L1 16.2338L1 1.76619Z" stroke="#A42BB9"
                                                          strokeWidth="2"/>
                                                </svg>
                                                </a>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        )
                    }
                </div>
            ))
        )
    };
    if (!isLoaded && !isMainPage) {
        return <Loader/>;
    } else {
        return (
            <>
                {
                    isMainPage ? (
                        <div className="books-section">
                            <div className="container">
                                {
                                    books && books.map((item) => (
                                        item.newBooks && (
                                            window.innerWidth <= 769 ?
                                                scrollInner(renderNewBooks(item))
                                                :
                                                renderNewBooks(item)
                                        )
                                    ))
                                }
                            </div>
                        </div>
                    ) : (
                        <>
                            <div className="page training-page books">
                                <div className="container">
                                    <div className="page-wrapper">
                                        <div className="page-content fw">
                                            <div className="page-content__wrap">
                                                <div className="page-content__title">
                                                    <h1>{books.map(item => item.title)}</h1>
                                                </div>
                                                {renderInner(isLoaded)}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </>
                    )
                }
            </>
        );
    }
};

export default observer(Books);