import React from 'react';
import {observer} from "mobx-react";
import Loader from "./Loader";
import {LazyLoadImage} from "react-lazy-load-image-component";
import GoogleFontLoader from "react-google-font-loader";

const Library = ({isLoaded, library}) => {
    const numerous = {
        0: 'first',
        1: 'second',
        2: 'third',
        3: 'fourth',
        4: 'fifth'
    };
    const titleSizes = {
        2: 'library-books-item__title--size',
        3: 'library-books-item__title--size-medium',
        4: 'library-books-item__title--size-big',
    };
    const menu = library.menu;
    const recommend = library.rek;
    const banner = library.banner;
    const popular = library.popular;
    const latest = library.last;
    let popular1;
    if(popular) {
        popular1 = popular.slice().sort(function(a, b) {
            if(a.name.toLowerCase() < b.name.toLowerCase()) return -1;
            if(a.name.toLowerCase() > b.name.toLowerCase()) return 1;
            return 0;
        })
    }
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <GoogleFontLoader
                    fonts={[
                        {
                            font: 'Playfair Display',
                            weights: [700],
                        }
                    ]}
                />
                <div className="page library-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                <div className="filters-list">
                                    <a className="filters-list__item filters-list__item_active">
                                        Общий раздел
                                    </a>
                                    {
                                        menu && menu.map((item, index) => (
                                            <a key={index} target="_blank" href={item.url} className="filters-list__item">
                                                {item.text}
                                            </a>
                                        ))
                                    }
                                </div>
                                <div className="sidebar-recomended">
                                    {/* <div className="block-themes">
                          инфо по темам:
                        </div> */}
                                    {
                                        recommend && <div className="sidebar-recomended__inner">
                                            <div className="sidebar-recomended__title">Рекомендуем <br/> к прочтению:
                                            </div>
                                            {
                                                Object.keys(recommend).map((item, index) => (
                                                    <div key={index}
                                                         className="sidebar-recomended__item latest-item latest-item--align-right">
                                                        <div className="latest-item__img">
                                                            <a href={recommend[index].url}
                                                               className="latest-item__link">
                                                                <LazyLoadImage
                                                                    alt={recommend[index].title}
                                                                    effect="blur"
                                                                    src={recommend[index].img}
                                                                />
                                                            </a>
                                                        </div>

                                                        <div className="latest-item__author">
                                                            {
                                                                recommend[index].author
                                                            }
                                                        </div>

                                                        <div className="latest-item__title">
                                                            <a href={recommend[index].url}
                                                               className="latest-item__link">
                                                                {
                                                                    recommend[index].title
                                                                }
                                                            </a>
                                                        </div>
                                                        {/*<div className="latest-item__datetime">*/}
                                                        {/*  14 часов назад*/}
                                                        {/*</div>*/}
                                                    </div>
                                                ))
                                            }
                                        </div>
                                    }
                                </div>
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{library.title}</h1>
                                    </div>
                                    {
                                        banner && <div className="page-content__banner">
                                            <div className="library-banner">
                                                <div className="library-books">
                                                    {
                                                        Object.keys(banner).map((item, index) => (
                                                            <div key={index}
                                                                 className={`library-books__item library-books-item books-item-${numerous[index]}`}>
                                                                <a
                                                                    href={banner[item].url}
                                                                    className={`library-books-item__title ${index > 1 ? titleSizes[index] : ''}`}
                                                                    dangerouslySetInnerHTML={{__html: banner[index].title}}
                                                                />
                                                                <div className="library-books-item__footer books-item-footer">
                                                                  <span className="books-item-footer__text">
                                                                      {banner[index].hover}
                                                                  </span>
                                                                </div>
                                                            </div>
                                                        ))
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                    }
                                    <div className="page-content__popular library-content library-popular">
                                        <div className="library-content__title">
                                            <span>Популярные темы статей</span>
                                        </div>
                                        <div className="library-popular__list library-popular-list">
                                            {
                                                popular1 && <ul className="library-popular-list__item popular-list">
                                                    {
                                                        Object.keys(popular1).map((item, index) => (
                                                            <li key={index} className="popular-list__item">
                                                                <a href={popular1[index].url} target={'_blank'} className="popular-list__link">{popular1[index].name}</a>
                                                            </li>
                                                        ))
                                                    }
                                                </ul>
                                            }
                                        </div>
                                    </div>
                                    {
                                        latest && <div className="page-content__latest library-content">
                                            <div className="library-content__title library-content__title--latest">
                                                <span>Последние поступления</span>
                                            </div>
                                            <div className="library-latest">
                                            {
                                                Object.keys(latest).map((item, index) => (
                                                    <div key={index}
                                                         className="library-latest__item latest-item">
                                                        <div className="latest-item__img">
                                                            <a target="_blank" href={latest[index].url}
                                                               className="latest-item__link">
                                                                <LazyLoadImage
                                                                    alt={latest[index].title}
                                                                    effect="blur"
                                                                    src={latest[index].img}
                                                                />
                                                            </a>
                                                        </div>

                                                        <div className="latest-item__author">
                                                            {
                                                                latest[index].author
                                                            }
                                                        </div>

                                                        <div className="latest-item__title">
                                                            <a target='_blank' href={latest[index].url}
                                                               className="latest-item__link">
                                                                {
                                                                    latest[index].title
                                                                }
                                                            </a>
                                                        </div>
                                                        {/*<div className="latest-item__datetime">*/}
                                                        {/*  14 часов назад*/}
                                                        {/*</div>*/}
                                                    </div>
                                                ))
                                            }
                                            </div>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(Library);