import React, { Component } from 'react';
import { inject, observer } from "mobx-react";
import Layout from '../Layout';
import {Seo} from "../component/Seo";
import get from "lodash/get";
import Slide from "../component/Slide";

@inject('slide', 'pages')
@observer
export default class SlidePage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const {match: {params: {id}}} = this.props;
        this.props.slide.getById(id);
        this.props.pages.getInfoSeo(id);
    }
    render() {
        const { slide: { info, isLoaded }, pages: {page}} = this.props;
        console.log(this.props)
        let infoL;
        if(info.length > 0) {
            infoL = get(info, '[0]')
        }
        return (
            <Layout className="pageWrap">
                <Seo page={page}/>
                <Slide info={infoL} isLoaded={isLoaded} />
            </Layout>
        );
    }
}