import React, {Component} from 'react';
import Swiper from "react-id-swiper";
import {Tab, TabList, TabPanel, Tabs} from "react-tabs";
import Loader from "./Loader";


class MainSlider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false,
            prevIndex: 0,
            tabIndex: 0,
            nextIndex: 1,
            hovered: false
        }
    }
    intervalID = undefined;
    componentDidUpdate() {
        if (this.state.hovered && !this.intervalID) {
            this.intervalID = setInterval(function () {
                this.handleSelect(
                    this.state.tabIndex !== 2 ? this.state.tabIndex+1 : 0,
                    this.state.tabIndex
                )
            }.bind(this), 10000)
        } else {
            clearInterval(this.intervalID);
            this.intervalID = undefined;
        }
    }

    componentWillUnmount() {
        clearInterval(this.intervalID);
    }

    handleSelect = (tabIndex, prevIndex) => {
        this.setState({
            prevIndex,
            tabIndex
        });
    };

    startlider() {
        this.setState({
            hovered: false
        })
    }

    stopSlider() {
        this.setState({
            hovered: true
        })
    }

    render() {
        const {mainSlider, isLoaded} = this.props;
        let style;
        if ((this.state.prevIndex === 1 && this.state.tabIndex === 2) || (this.state.prevIndex === 0 && this.state.tabIndex === 2)) {
            style = '.main-slider .react-tabs .main-slider__wrapper .main-slider__item:nth-child(2) .photo-slider {transform: translateX(-50%);}' +
                '.main-slider .react-tabs .main-slider__wrapper .main-slider__item:nth-child(2)::after {transform: translateX(-500px);}'
        } else {
            style = '';
        }
        const params = {
            shouldSwiperUpdate: false,
            rebuildOnUpdate: false,
            pagination: {
                el: '.swiper-pagination',
                type: 'bullets',
                clickable: true,
                renderBullet: (index, className) => {
                    return '<span class="' + className + '"></span>';
                }
            },
            observer: true,
            observeParents: true,
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 56,
            setWrapperSize: true,
            breakpoints: {
                320: {
                    slidesPerView: 1,
                    slidesPerGroup: 1,
                    width: 117,
                    spaceBetween: 20,
                },
                600: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                    spaceBetween: 30
                },
                1024: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                },
                1025: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                }
            }
        };
        if(isLoaded) {
            return (
                <div className='main-slider'>
                    <style dangerouslySetInnerHTML={{__html: style}}/>
                    {
                        mainSlider && (
                            <Tabs
                                selectedIndex={this.state.tabIndex}
                                onSelect={(tabIndex, prevIndex) => this.handleSelect(tabIndex, prevIndex)}
                                forceRenderTabPanel={true}
                            >
                                <TabList className={'main-slider__buttons'}>
                                    {
                                        Object.keys(mainSlider).map((slide, i) => (
                                                <Tab key={i}>
                                                    <div dangerouslySetInnerHTML={{__html: mainSlider[slide][0].title}}/>
                                                </Tab>
                                            )
                                        )
                                    }
                                </TabList>
                                <div className="main-slider__wrapper">
                                    {
                                        Object.keys(mainSlider).map((slide, i) => (
                                                <TabPanel className="main-slider__item" key={i}
                                                          onMouseEnter={() => this.startlider()}
                                                          onMouseLeave={() => this.stopSlider()}
                                                >
                                                    <style dangerouslySetInnerHTML={{
                                                        __html: `
                                               .main-slider .react-tabs .main-slider__wrapper .main-slider__item:nth-child(${i + 1})::after{
                                                    background-image: url(${mainSlider[slide][0].background});
                                               } 
                                            `
                                                    }}/>
                                                    <div className="main-slider__item--content">
                                                        <div className="main-slider__item--head">
                                                            <h2 dangerouslySetInnerHTML={{__html: mainSlider[slide][0].title}}/>
                                                            <a href={`/#/${mainSlider[slide][0].url}`}>
                                                                в раздел →
                                                            </a>
                                                        </div>
                                                        {
                                                            i === 2 ? (
                                                                <div className="main-slider__item--list-video">
                                                                    {
                                                                        this.state.tabIndex === 2 && (
                                                                            <Swiper {...params} >
                                                                                {
                                                                                    mainSlider[slide][0].list.map(item => (
                                                                                        <div key={Math.random()}>
                                                                                            <a className="gradient" href={item.url}>
                                                                                                <img src={item.img} alt="image" className={"swiper-lazy"}/>
                                                                                            </a>
                                                                                            <a href={`${item.url}`}>
                                                                                                {item.title}
                                                                                            </a>
                                                                                        </div>
                                                                                    ))
                                                                                }
                                                                            </Swiper>
                                                                        )
                                                                    }
                                                                </div>
                                                            ) : (
                                                                <div className="main-slider__item--list">
                                                                    {
                                                                        mainSlider[slide][0].list.map(item => (
                                                                            <a href={`/#/${item.url}`} key={Math.random()}>
                                                                                <div className="safari-img">
                                                                                    <img src={item.icon} alt=""/>
                                                                                </div>
                                                                                {item.text}
                                                                            </a>
                                                                        ))
                                                                    }
                                                                </div>
                                                            )
                                                        }
                                                    </div>
                                                    <div
                                                        className={'photo-slider'}>
                                                        <img src={mainSlider[slide][0].photo} alt="photo1"/>
                                                    </div>
                                                </TabPanel>
                                            )
                                        )
                                    }
                                </div>
                            </Tabs>
                        )
                    }
                </div>
            )
        } else {
            return (
                <Loader isMainPage={true} />
            )
        }
    }
}

export default MainSlider;