import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import Layout from '../Layout';
import EventsList from '../component/EventsList';
import _get from 'lodash/get';
import {Seo} from "../component/Seo";

@inject('ui', 'events', 'pages')
@withRouter
@observer
class EventsPage extends Component {
    componentDidMount() {
        this.props.pages.getById(330);
        // document.body.scrollTop = 0;
        // document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        const page = _get(this.props, 'match.params.id', '');
        this.props.events.getAll(category, page);
    };

    componentDidUpdate() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        const page = _get(this.props, 'match.params.id', '');
        this.props.events.getAll(category, page);
    };

    goToCat = slug => {
        const url = slug ? `/events/category/${slug}` : '/events';
        this.props.history.push(url);
    };

    goNoCatToPage = id => {
        const url = id === 1 ? '/events' : `/events/page/${id}`;
        this.props.history.push(url);
    };

    goCatToPage = (slug, id) => {
        const url = id === 1 ? `/events/category/${slug}` : `/events/category/${slug}/page/${id}`;
        this.props.history.push(url);
    };

    render() {
        const {ui, events: {events, cats, isLoaded, nextVebinar}, pages: {page}} = this.props;
        let vebinar;
        if(events.length > 0) {
            vebinar = _get(nextVebinar, '[0]')
        }
        const currentPage = _get(this.props, 'match.params.id', '');
        const category = _get(this.props, 'match.params.cat', '');
        let catTitle;
        cats.map(cat => (
            (cat.id === category || !category) ?
                cat.id === category ? (catTitle = cat.name) : null
                : null
        ));
        return (
            <Layout className="pageWrap">
                <Seo page={page} catTitle={catTitle} currentPage={currentPage} />
                <EventsList
                    events={events}
                    isLoaded={isLoaded}
                    category={category}
                    cats={cats}
                    goToCat={this.goToCat}
                    goNoCatToPage={this.goNoCatToPage}
                    goCatToPage={this.goCatToPage}
                    vebinar={vebinar}
                />
            </Layout>
        );
    };
}

export default EventsPage;