import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Donate {
    @observable isLoaded = true;
    @observable donate = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Donate.getAll()
                .then(action(({donate}) => {
                    this.donate = donate || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Donate();