import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import Event from '../component/Event';
import {Seo} from "../component/Seo";
import get from "lodash/get";

@inject('ui', 'events', 'pages')
@observer
class EventPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const {match: {params: {id}}} = this.props;
        this.props.events.setEventId(id);
        this.props.events.getEvent(id);
        this.props.pages.getEventSeo(id);
    }

    // componentDidUpdate() {
        // const currentPage = _get(this.props, 'events.lastPage');
        // const id = _get(this.props, 'match.params.id', '');
        // this.props.events.getEvent(id);
    // }

    goToCat = slug => {
        const url = slug ? `/events/category/${slug}` : '/events';
        this.props.history.push(url);
    };

    render() {
        const {ui, events: {isLoaded, cats, category}, pages: {page}} = this.props;
        const event = this.props.events.event;
        let redneredContent;
        if(page) {
            redneredContent = get(page, 'content.rendered')
        }
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Event event={event} redneredContent={redneredContent} cats={cats} category={category} isLoaded={isLoaded} goToCat={this.goToCat}/>
            </Layout>
        );
    }
}

export default EventPage;