import React, { Component } from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import About from "../component/About";
import {Seo} from "../component/Seo";
import get from "lodash/get";

@inject('ui', 'about', 'pages')
@observer
class AboutPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.about.getAll();
        this.props.pages.getById(121);
    }
    toggleMenu = open => {
        const {ui: {openMenu}} = this.props;
        const status = typeof open === 'undefined' ? !openMenu :  open;

        this.props.ui.toggleMenu(status)
    };
    isActive = page => {
        const path = this.props.match.path;
        return path.indexOf(page) !== -1 ? 'filters-list__item_active' : '';
    };
    handleClick = () => {
        this.toggleMenu(false);
    };
    render() {
        const { ui, about: { about, isLoaded }, pages: {page}} = this.props;
        let redneredContent;
        if(page) {
            redneredContent = get(page, 'content.rendered')
        }
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <About about={about} redneredContent={redneredContent} isLoaded={isLoaded} isActive={this.isActive} handleClick={this.handleClick} />
                {/*<TrainingForm  />*/}
            </Layout>
        );
    }
}

export default AboutPage;