import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Infos {
    @observable isLoaded = true;
    @observable infos = [];
    @observable info = [];
    @observable infoId = 0;
    @observable cats = [];
    @observable nextVebinar = [];
    @observable lastCategory = null;
    @observable lastPage = null;

    @action setInfoId(id){
        this.infoId = id;
    }

    @action getAll(category, page) {
        if (category !== this.lastCategory && this.isLoaded || page !== this.lastPage && this.isLoaded) {
            this.isLoaded = false;
            this.lastCategory = category;
            this.lastPage = page;
            return agent.Infos.getAll(category, page)
                .then(action(({infos, cats, nextVebinar}) => {
                    this.infos = infos || [];
                    this.cats = cats || [];
                    this.nextVebinar = nextVebinar || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
    @action getInfo(id) {
        if(id && this.isLoaded) {
            this.isLoaded = false;
            return agent.Infos.getId(id)
                .then(action(({info, cats}) => {
                    this.info = info || [];
                    this.cats = cats || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Infos();