import React, {Component} from 'react';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import {Link} from "react-router-dom";
import Scrollbar from "react-scrollbars-custom";

class EventListGallery extends Component {
    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false
        }
    }

    onClickImgHandle = (index) => {
        this.setState({photoIndex: index, isOpen: true})
    };

    openLightBox = (isOpen, images, photoIndex) => {
        return isOpen && (
            <Lightbox
                mainSrc={images[photoIndex]}
                nextSrc={images[(photoIndex + 1) % images.length]}
                prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                onCloseRequest={() => this.setState({isOpen: false})}
                animationDisabled={false}
                onMovePrevRequest={() =>
                    this.setState({
                        photoIndex: (photoIndex + images.length - 1) % images.length,
                    })
                }
                onMoveNextRequest={() =>
                    this.setState({
                        photoIndex: (photoIndex + 1) % images.length,
                    })
                }
            />
        )
    };

    render() {
        const {gallery, linkTo, maxLength} = this.props;
        let images = [];
        Object.keys(gallery).map((src, index) => {
            if (images.length === maxLength) {
                return
            }
            images.push(gallery[index]);
        });
        const {photoIndex, isOpen} = this.state;
        if (images.length === 0) {
            return null;
        }
        if (maxLength === 7) {
            return (
                <>
                    <Scrollbar
                        style={{ width: "100%", height: 258 }}
                    >
                        <div className={'events-page__single--gallery'}>
                            <div className={'events-page__list--single-item'}>
                                <div
                                    className="events-page__list--gallery-item"
                                    style={{backgroundImage: `url(${images[0]})`}}
                                    onClick={() => this.onClickImgHandle(0)}
                                />
                            </div>
                            <div className="events-page__list--gallery">
                                {
                                    images.map((src, index) => {
                                        if (index !== 0) {
                                            return (
                                                <div
                                                    key={index}
                                                    className="events-page__list--gallery-item"
                                                    style={{backgroundImage: `url(${src})`}}
                                                    onClick={() => this.onClickImgHandle(index)}
                                                />
                                            );
                                        }
                                    })
                                }
                            </div>
                        </div>
                    </Scrollbar>

                    <a href="/#/gallery" className={'full-gallery-link'}>
                        Все фотографии 
                        {/* тут может надо выводить кол-во всех фото в галерее?  */}
                        <span className="full-gallery-link__count">({images.length})</span>

                        <svg xmlns="http://www.w3.org/2000/svg" width="11" height="12"
                            viewBox="0 0 11 12" fill="none">
                            <path d="M1.42139 10.5816V1.41846L9.86448 6.00144L1.42139 10.5816Z"
                                stroke="#3BA9DC" strokeWidth="1.5" strokeMiterlimit="10"
                                strokeLinecap="round" strokeLinejoin="round"/>
                        </svg>
                    </a>
                    {this.openLightBox(isOpen,images,photoIndex)}
                </>
            )
        } else {
            return (
                <>
                    <div className="events-page__list--gallery">
                        {
                            images.map((src, index) => {
                                return (
                                    <div
                                        key={index}
                                        className="events-page__list--gallery-item"
                                        style={{backgroundImage: `url(${src})`}}
                                        onClick={() => this.onClickImgHandle(index)}
                                    />
                                );
                            })
                        }
                        {maxLength === 5 && <Link to={linkTo} className="events-page__list--gallery-last">
                            <span>все<br/>{gallery.length} фото</span>
                        </Link>}
                    </div>
                    {this.openLightBox(isOpen,images,photoIndex)}
                </>
            )
        }

    }
}

export default EventListGallery;