import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Photos {
    @observable isLoaded = true;
    @observable photos = [];
    @observable photoId = 0;
    @observable cats = [];
    @observable lastCategory = null;
    @observable lastPage = null;

    @computed get photo(){
        return this.photos.filter(photo => photo.id == this.photoId)[0]
    };

    @action setPhotoId(id){
        this.photoId = id;
    }
    @action getAll(category, page) {
        if (category !== this.lastCategory && this.isLoaded || page !== this.lastPage && this.isLoaded) {
            this.isLoaded = false;
            this.lastCategory = category;
            this.lastPage = page;
            return agent.Photos.getAll(category, page)
                .then(action(({photos, cats}) => {
                    this.photos = photos || [];
                    this.cats = cats || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
    @action getMainPhotos() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Photos.getMainPhotos()
                .then(action(({photos}) => {
                    this.photos = photos || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Photos();