import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Slide {
    @observable isLoaded = true;
    @observable info = [];
    @observable lastId = null;

    @action getById(id) {
        if (this.isLoaded && id !== this.lastId) {
            this.isLoaded = false;
            return agent.Slide.getById(id)
                .then(action(({info}) => {
                    this.info = info || [];
                    this.isLoaded = true;
                    this.lastId = id;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Slide();