import React, { Component } from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import Training from "../component/Training";
import {Seo} from "../component/Seo";
// import FullWidthForm from "../component/FullWidthForm";

@inject('ui', 'training', 'pages')
@observer
class TrainingPage extends Component {
    componentDidMount() {
        this.props.pages.getById(104);
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.training.getAll();
    }
    render() {
        const { ui, training: { training, isLoaded }, pages: {page}} = this.props;
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Training training={training} isLoaded={isLoaded} />
                {/*<TrainingForm  />*/}
            </Layout>
        );
    }
}

export default TrainingPage;