import React from 'react';
import {createPaginationArray} from "./CreatePaginationArray";

export const Pagination = (props) => {
    const {currentPage, totalPages, goNoCatToPage, goCatToPage, category} = props;
    const paginationArray = createPaginationArray(currentPage, totalPages);
    return (
        <div>
            <nav className={"pagination"}>
                <ul>
                    {
                        !category ? (
                            paginationArray.map((pageNo, index) => (
                                <li key={index} className={parseInt(currentPage) === pageNo ? 'active' : ''} onClick={() => goNoCatToPage(pageNo)}>{pageNo}</li>
                            ))
                        ) : (
                            paginationArray.map((pageNo, index) => (
                                <li key={index} className={parseInt(currentPage) === pageNo ? 'active' : ''} onClick={() => goCatToPage(category, pageNo)}>{pageNo}</li>
                            ))
                        )
                    }
                </ul>
            </nav>
        </div>
    )
};

