import React, {useState} from "react";
import _uniqueId from 'lodash/uniqueId';
import InputMask from "react-input-mask";

const FromInput = (props) => {
    const {error, type, style, name, handleInputChange, placeholder, value, aboutPage} = props;
    const [id] = useState(_uniqueId('prefix-'));

    return (
        <div className={`form__group field ${error ? 'error' : ''} ${aboutPage ? 'singup-form__field' : ''}`} style={style}>
            {
                name !== 'phone' ? (
                    <input type={type} className={`form__field ${value ? 'active' : ''}`} name={name} id={id} placeholder={placeholder} onChange={handleInputChange}
                           value={value}
                    />
                ) : (
                    <InputMask value={value} name='phone' className={`form__field ${value ? 'active' : ''}`} id={id} mask="+7 (999) 999-99-99" placeholder={placeholder} onChange={handleInputChange} />
                )
            }
            <label htmlFor={id} className={'form__label'}>{placeholder}</label>
        </div>
    )
};
export default FromInput;