import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Tvs {
    @observable isLoaded = true;
    @observable tvs = [];
    @observable tvId = 0;
    @observable cats = [];
    @observable nextVebinar = [];
    @observable lastCategory = null;

    @computed get tv(){
        return this.tvs.filter(tv => tv.id == this.tvId)[0]
    };

    @action setTvId(id){
        this.tvId = id;
    }
    @action getAll(category) {
        if (category !== this.lastCategory && this.isLoaded) {
            this.isLoaded = false;
            this.lastCategory = category;
            return agent.Tvs.getAll(category)
                .then(action(({tvs, cats, nextVebinar}) => {
                    this.tvs = tvs || [];
                    this.cats = cats || [];
                    this.nextVebinar = nextVebinar || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Tvs();