import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import Loader from "./Loader";

const TVpages = ({title, id, content, isLoaded, isActive, handleClick}) => {
    const renderInner = () => {
        return (
            <div className="teachers-page__content" key={id}>
                <div className="post__content-inner" dangerouslySetInnerHTML={{__html: content}}/>
            </div>
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <div className="page teachers-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                <div className="filters-list">
                                    <Link to='/tv' onClick={handleClick}
                                          className={`filters-list__item ${isActive('tv')}`}>Каббала ТВ</Link>
                                    <Link to='/tv/mak-tv' onClick={handleClick}
                                          className={`filters-list__item ${isActive('mak-tv')}`}>МАК ТВ</Link>
                                    <Link to='/tv/zoar-tv' onClick={handleClick}
                                          className={`filters-list__item ${isActive('zoar-tv')}`}>Зоар ТВ</Link>
                                    <Link to='/tv/tajny-vechnoj-knigi' onClick={handleClick}
                                          className={`filters-list__item ${isActive('tajny-vechnoj-knigi')}`}>Тайны вечной книги</Link>
                                </div>
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{title}</h1>
                                    </div>
                                    {renderInner(isLoaded)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(TVpages);