import superagentPromise from 'superagent-promise';
import _superagent from 'superagent';

import config from './config.js';
import ui from './stores/ui'

const API = () => `${config.API_ROOT}/wp-json/`;
const superagent = superagentPromise(_superagent, global.Promise);

const handleErrors = err => err;

const responseBody = res => {
    if (typeof res.body === 'string') {
        return JSON.parse(res.body)
    }
    return res.body;
};

const requests = {
    del: url => superagent
        .del(`${API(ui)}${url}`)
        .end(handleErrors)
        .then(responseBody),
    get: (url) => superagent
        .get(`${API(ui)}${url}`)
        .end(handleErrors)
        .then(responseBody),
    put: (url, body) => superagent
        .put(`${API(ui)}${url}`, body)
        .end(handleErrors)
        .then(responseBody),
    post: (url, body) => superagent
        .post(`${API(ui)}${url}`, body)
        .end(handleErrors)
        .then(responseBody),
};

const General = {
    get: () => requests.get('index/data'),
    getOptions: () => requests.get('options/all'),
    getFooterMenu: () => requests.get('menus/v1/menus/footer-menu'),
    getMainMenu: () => requests.get('menus/v1/menus/main-menu'),
    getLangMenu: () => requests.get('menus/v1/menus/lang-menu'),
};

const Events = {
    getAll: (cat, page) => requests.get(`events/list?limit=6&cat=${cat || ''}&page=${page || ''}`),
    getId: id => requests.get(`event/list?&id=${id || ''}`)
};

const Infos = {
    getAll: (cat, page) => requests.get(`infos/list?limit=6&cat=${cat || ''}&page=${page || ''}`),
    getId: id => requests.get(`info/list?&id=${id || ''}`)
};

const Tvs = {
    getAll: (cat, page) => requests.get(`tv/list?limit=200&cat=${cat || ''}&page=${page || ''}`)
};

const Photos = {
    getAll: (cat, page) => requests.get(`gallery/list?limit=18&cat=${cat || ''}&page=${page || ''}`),
    getMainPhotos: () => requests.get(`gallery/list?limit=4&cat=`)
};

const Teachers = {
    getAll: () => requests.get('teachers/list')
};

const Training = {
    getAll: () => requests.get('training/list')
};

const About = {
    getAll: () => requests.get('about/list')
};

const Library = {
    getAll: () => requests.get('library/list')
};

const Books = {
    getAll: () => requests.get('books/list')
};

const Donate = {
    getAll: () => requests.get('donate/list')
};

const Pages = {
    getById: id => requests.get(`wp/v2/pages/${id}`),
    getEventSeo: id => requests.get(`wp/v2/events/${id}`),
    getInfoSeo: id => requests.get(`wp/v2/13veshey/${id}`),
    getInfosSeo: id => requests.get(`wp/v2/posts/${id}`),
};

const Slide = {
    getById: (id) => requests.get(`slide/list?id=${id}`),
};

export default {
    General,
    Events,
    Tvs,
    Photos,
    Teachers,
    Training,
    About,
    Books,
    Donate,
    Pages,
    Library,
    Infos,
    Slide,
}