import {observable, action} from 'mobx';
import agent from '../agent';

export class UI {
    @observable errors = null;
    @observable isMobile = window.outerWidth < 800;
    @observable isOpenBanner = false;
    @observable openMenu = false;
    @observable modals = false;

    @observable options = {
        email: '',
        phone: '',
        facebook: '',
        twitter: '',
        instagram: '',
        google_plus: '/',
        name: '',
        description: '',
    };

    @observable footmenu = [];
    @observable mainmenu = [];
    @observable langmenu = [];

    @observable isLoaded = true;

    @observable mainData = {
        events: [],
        mainText: ''
    };
    @action setError = err => {
        this.errors = err.response && err.response.body && err.response.body.errors;
    };
    @action toggleMenu = (open) => {
        if (this.isMobile) {
            this.openMenu = open;
        }
    };
    @action toggleModals = (modals) => {
        this.modals = modals;
    };
    @action pushTo (history, to){
        history.push(to);
    }
    @action loadGeneralData() {
        this.isLoaded = false;
        return agent.General.get()
            .then(action(({options, ...rest}) => {
                this.options = options;
                this.mainData = rest;
            }))
            .catch(action(err => {
                this.setError(err);
                throw err;
            }))
            .finally(action(() => {
                this.isLoaded = true;
            }));
    }

    @action getOptions() {
        return agent.General.getOptions()
            .then(action((options) => {
                this.options = options;
            }))
            .catch(action(err => {
                this.setError(err);
                throw err;
            }))
    }

    @action getFooterMenu() {
        return agent.General.getFooterMenu()
            .then(action((footmenu) => {
                this.footmenu = footmenu;
            }))
            .catch(action(err => {
                this.setError(err);
                throw err;
            }))
    }

    @action getMainMenu() {
        return agent.General.getMainMenu()
            .then(action((mainmenu) => {
                this.mainmenu = mainmenu;
            }))
            .catch(action(err => {
                this.setError(err);
                throw err;
            }))
    }

    @action getLangMenu() {
        return agent.General.getLangMenu()
            .then(action((langmenu) => {
                this.langmenu = langmenu;
            }))
            .catch(action(err => {
                this.setError(err);
                throw err;
            }))
    }

    async asyncInit() {
        this.getOptions();
        this.getFooterMenu();
        this.getMainMenu();
        this.getLangMenu();
    }
}

const UIStore = new UI();
UIStore.asyncInit();

export default UIStore;