import React, {Component} from 'react';
import Header from './component/Header';
import Footer from './component/Footer';
import {observer, inject} from "mobx-react";

@inject('ui')
@observer
class Layout extends Component {
    toggleMenu = open => {
        const {ui: {openMenu}} = this.props;
        const status = typeof open === 'undefined' ? !openMenu : open;

        this.props.ui.toggleMenu(status)
    };

    render() {
        const {ui: {options, openMenu, footmenu, mainmenu, langmenu}, children, black = true, className, isMainPage} = this.props;
        const footerMenu = footmenu.items;
        const topMenu = mainmenu.items;
        const langMenu = langmenu.items;
        return (
            <div id={"outer-container"}
                className={`wrapper ${!!openMenu ? 'wrapper_open' : ''} ${black ? 'wrapper_black' : ''} ${className || ''}`}>
                <Header
                    toggleMenu={this.toggleMenu}
                    indexHeader='true'
                    isMainPage={isMainPage}
                    // black={black}
                    mainMenu={topMenu}
                    langMenu={langMenu}
                />
                <main id={'mwrap'} className={`${!isMainPage ? 'bg_mask' : ''}`}>
                    {children}
                </main>
                <Footer options={options} footerMenu={footerMenu} />
            </div>
        );
    }
}

export default Layout;