import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import Loader from "./Loader";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Teachers = ({teachers, isLoaded, isActive, handleClick}) => {
    const renderInner = () => {
        return (
            teachers.map((item) => (
                <div className="teachers-page__content" key={item.title}>
                    {
                        item.vstupText && (
                            <div className="teachers-page__vstup">
                                <p>
                                    {item.vstupText}
                                </p>
                            </div>
                        )
                    }
                    {
                        item.img && (
                            <div className="teachers-page__img">
                                <LazyLoadImage
                                    alt={item.title}
                                    effect="blur"
                                    src={item.img}
                                />
                            </div>
                        )
                    }
                    {
                        item.teachersColumns && (
                            <div className="teachers-page__columns">
                                {
                                    item.teachersColumns.map(teacher => (
                                        <div className="teachers-page__columns--item" key={teacher.name + teacher.img}>
                                            <div className="teachers-page__columns--mobile">
                                                {
                                                    teacher.photo && (
                                                        <LazyLoadImage
                                                            alt={teacher.name}
                                                            effect="blur"
                                                            src={teacher.photo}
                                                        />
                                                    )
                                                }
                                                {
                                                    teacher.name && (
                                                        <h2>{teacher.name}</h2>
                                                    )
                                                }
                                            </div>
                                            {
                                                teacher.description && (
                                                    <div dangerouslySetInnerHTML={{__html: teacher.description}}/>
                                                )
                                            }
                                        </div>
                                    ))
                                }
                            </div>
                        )
                    }
                    {
                        item.teachersFw && (
                            <div className="teachers-page__fw">
                                {
                                    item.teachersFw.map(teacher => (
                                        <div className="teachers-page__fw--item" key={teacher.name + teacher.photo}>
                                            {
                                                teacher.photo && (
                                                    <div className="teachers-page__fw--photo">
                                                        <LazyLoadImage
                                                            alt={teacher.name}
                                                            effect="blur"
                                                            src={teacher.photo}
                                                        />
                                                    </div>
                                                )
                                            }
                                            <div className="teachers-page__fw--content">
                                                {
                                                    teacher.name && (
                                                        <h2>{teacher.name}</h2>
                                                    )
                                                }
                                                {
                                                    teacher.description && (
                                                        <div dangerouslySetInnerHTML={{__html: teacher.description}}/>
                                                    )
                                                }
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        )
                    }
                </div>
            ))
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <div className="page teachers-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                <div className="filters-list">
                                    <Link to='/about' onClick={handleClick}
                                          className={`filters-list__item ${isActive('about')}`}>Об ассоциации</Link>
                                    <Link to='/author' onClick={handleClick}
                                          className={`filters-list__item ${isActive('author')}`}>О Михаэле Лайтмане</Link>
                                    <Link to='/projects' onClick={handleClick}
                                          className={`filters-list__item ${isActive('projects')}`}>Проекты академии</Link>
                                    <Link to='/teachers' onClick={handleClick}
                                          className={`filters-list__item ${isActive('teachers')}`}>Преподаватели</Link>
                                    <Link to='/contacts' onClick={handleClick}
                                          className={`filters-list__item ${isActive('contacts')}`}>Контакты</Link>
                                </div>
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{teachers.map(item => item.title)}</h1>
                                    </div>
                                    {renderInner(isLoaded)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(Teachers);