import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Training {
    @observable isLoaded = true;
    @observable training = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Training.getAll()
                .then(action(({training}) => {
                    this.training = training || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Training();