import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Books {
    @observable isLoaded = true;
    @observable books = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Books.getAll()
                .then(action(({books}) => {
                    this.books = books || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Books();