import React from 'react';
import ReactDOM from 'react-dom';
// import {render} from 'react-snapshot';
import './style/libs.min.css';
import './style/main.css';
import stores from './stores/index';
import {inject, observer, Provider} from 'mobx-react';
import {HashRouter as Router, Redirect, Route, Switch} from "react-router-dom";
import EventsPage from "./pages/EventsPage";
import EventPage from "./pages/EventPage";
import TvsPage from "./pages/TvsPage";
import PhotosPage from "./pages/PhotosPage";
import TeachersPage from "./pages/TeachersPage";
import TrainingPage from "./pages/TrainingPage";
import AboutPage from "./pages/AboutPage";
import BooksPage from "./pages/BooksPage";
import DonatePage from "./pages/DonatePage";
import LibraryPage from "./pages/LibraryPage";
import AppPage from "./pages/AppPage";
import Pages from "./pages/Pages";
import InfoPage from "./pages/InfoPage";
import AboutLaytman from "./pages/AboutLaytman";
import ProjectsPage from "./pages/ProjectsPage";
import ContactsPage from "./pages/ContactsPage";
import InfosPage from "./pages/InfosPage";
import SlidePage from "./pages/SlidePage";
import GoogleFontLoader from 'react-google-font-loader';
import MakTV from "./pages/MakTV";
import ZoarTV from "./pages/ZoarTV";
import TayniTV from "./pages/TayniTV";

const AppMobx = observer(inject('ui')(App));
ReactDOM.render(
    <Provider {...stores}>
        <Router>
            <Switch>
                <Route path="/:locale" component={AppMobx}/>
                <Route path="/" component={AppMobx}/>
            </Switch>
        </Router>
    </Provider>,
    document.getElementById('root')
);

function App({match, ui}) {
    let root = '';
    return (
        <>
            <GoogleFontLoader
                fonts={[
                    {
                        font: 'Montserrat',
                        weights: [300, 500, 600],
                    },
                    {
                        font: 'Open Sans',
                        weights: [300],
                    },
                ]}
            />
            <Switch>
                <Route exact path={`${root}/`} component={AppPage}/>
                <Route exact path={`${root}/events`} component={EventsPage}/>
                <Route exact path={`${root}/events/category/:cat`} component={EventsPage}/>
                <Route exact path={`${root}/events/category/:cat/page/:id`} component={EventsPage}/>
                <Route exact path={`${root}/events/page/:id`} component={EventsPage}/>
                <Route exact path={`${root}/events/:id`} component={EventPage}/>
                <Route exact path={`${root}/info`} component={InfosPage}/>
                <Route exact path={`${root}/info/category/:cat`} component={InfosPage}/>
                <Route exact path={`${root}/info/category/:cat/page/:id`} component={InfosPage}/>
                <Route exact path={`${root}/info/page/:id`} component={InfosPage}/>
                <Route exact path={`${root}/info/:id`} component={InfoPage}/>
                <Route exact path={`${root}/tv`} component={TvsPage}/>
                <Route exact path={`${root}/tv/mak-tv`} component={MakTV}/>
                <Route exact path={`${root}/tv/zoar-tv`} component={ZoarTV}/>
                <Route exact path={`${root}/tv/tajny-vechnoj-knigi`} component={TayniTV}/>
                {/*<Route exact path={`${root}/tv/category/:cat`} component={TvsPage}/>*/}
                <Route exact path={`${root}/gallery`} component={PhotosPage}/>
                <Route exact path={`${root}/gallery/page/:id`} component={PhotosPage}/>
                <Route exact path={`${root}/gallery/:cat`} component={PhotosPage}/>
                <Route exact path={`${root}/gallery/:cat/page/:id`} component={PhotosPage}/>
                <Route exact path={`${root}/teachers`} component={TeachersPage}/>
                <Route exact path={`${root}/training`} component={TrainingPage}/>
                <Route exact path={`${root}/about`} component={AboutPage}/>
                <Route exact path={`${root}/o-mihaele-lajtmane`} component={AboutLaytman}/>
                <Route exact path={`${root}/projects`} component={ProjectsPage}/>
                <Route exact path={`${root}/contacts`} component={ContactsPage}/>
                <Route exact path={`${root}/books`} component={BooksPage}/>
                <Route exact path={`${root}/donate`} component={DonatePage}/>
                <Route exact path={`${root}/library`} component={LibraryPage}/>
                <Route exact path={`${root}/page/:id`} component={Pages}/>
                <Route exact path={`${root}/slide/:id`} component={SlidePage}/>
                <Redirect to={root}/>
            </Switch>
        </>
    )
}
