import React, {Component} from 'react';
import FormRender from "./Forms";
import {LazyLoadImage} from "react-lazy-load-image-component";

class MainSectionFour extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false,
        }
    }

    render() {
        const {mainTraining} = this.props;
        return (
            <>
                <div className={'section-four bg-white'}>
                    <div className="container">
                        <div className="columns">
                            <div className="page-content__form">
                                <FormRender fullForm={true} />
                            </div>
                            <div className="training">
                                <h2>
                                    Форматы обучения
                                </h2>
                                <div className="events-page__list">
                                    {mainTraining && Object.keys(mainTraining).map((key, index) => (
                                            <div className={'events-page__list--item'} key={index}>
                                                <div className="events-page__list--image"
                                                    style={{backgroundImage: `url(${mainTraining[key].img})`}}
                                                >
                                                    <LazyLoadImage
                                                        alt={mainTraining[key].title}
                                                        effect="blur"
                                                        src={mainTraining[key].img}
                                                    />
                                                </div>
                                                <div className="events-page__list--content">
                                                    <div className="events-page__list--title">
                                                        <h3>
                                                            {mainTraining[key].title}
                                                        </h3>
                                                    </div>
                                                    <div className="events-page__list--text"
                                                         dangerouslySetInnerHTML={{__html: mainTraining[key].description}}/>
                                                </div>
                                            </div>
                                        )
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

export default MainSectionFour;