import React, {Component} from 'react';
import Loader from './Loader';
import FormInput from './FormInput';
import host from "../Host";
import Rodal from "rodal";
import {inject, observer} from "mobx-react";
// import ga from 'react-ga';
// ga.initialize('UA-129290557-1');

const validateEmail = function (email) {
    if (email === '') return false;
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
};

@observer
@inject('ui')
class FormRender extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            phone: '',
            message: '',
            formSucces: false,
            firstTime: true,
            trainingPage: this.props.trainingPage,
            fullForm: this.props.fullForm,
            tvPage: this.props.tvPage,
            aboutPage: this.props.aboutPage,
            mainForm: this.props.mainForm,
            modal: this.props.modal,
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        // this.handleSubmit = this.handleSubmit.bind(this);
        this.formValidator = this.formValidator.bind(this);
        this.handleSubmitFullForm = this.handleSubmitFullForm.bind(this);
        this.handleSubscribeSubmit = this.handleSubscribeSubmit.bind(this);
        // this.handleChangeForm = this.handleChangeForm.bind(this);
    }

    // handleChangeForm(full) {
    //     this.setState({fullForm: full})
    // }

    handleInputChange(event) {
        event.preventDefault();
        let nameInput = event.target.name;
        let value = event.target.value;
        let stateObject = function () {
            let returnObj = {};
            returnObj[nameInput] = value;
            return returnObj;
        }();
        this.setState(stateObject);
    }

    getPreparedData = (state = {}) => {
        let store = {...state};
        store = {
            ...store
        };
        return store
    };

    formValidator(subForm) {
        let formState = this.getPreparedData(this.state);
        for (let formKey in formState) {
            let value = formState[formKey];
            if (!subForm) {
                if(!this.state.modal) {
                    if (formKey === 'phone' || formKey === 'name') {
                        if (!value) {
                            return false;
                        }
                    }
                } else {
                    if (formKey === 'message' || formKey === 'name') {
                        if (!value) {
                            return false;
                        }
                    }
                }
            } else {
                if (formKey === 'name') {
                    if (!value) {
                        return false;
                    }
                }
            }
            if (formKey === 'email') {
                if (!validateEmail(value)) {
                    return false
                }
            }

        }
        return true;
    }

    handleSubscribeSubmit(e) {
        e.preventDefault();
        let validForm = this.formValidator(true);
        if (validForm) {
            let stateObj = this.getPreparedData(this.state),
                name = stateObj.name,
                email = stateObj.email;

            let xhr = new XMLHttpRequest();

            let body = 'type=subscription' +
                '&name=' + encodeURIComponent(name) +
                '&email=' + encodeURIComponent(email);

            xhr.open("POST", 'http://kabacademy.com/newsletter/input.php', true);
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            xhr.send(body);
            // ga.event({
            //     category: 'User',
            //     action: 'Order'
            // });
            this.setState({formSucces: true, name: '', email: ''});
        } else {
            this.setState({firstTime: false})
        }
    }

    hide() {
        this.props.ui.toggleModals(false);
        this.setState({ formSucces: false });
    }

    handleSubmitFullForm(e) {
        e.preventDefault();
        let validForm = this.formValidator(false);
        if (validForm) {
            const {email, name, phone, message} = this.state;
            let xhr = new XMLHttpRequest();
            let body;
            if(this.state.modal) {
                body = '&email=' + encodeURIComponent(email) + '&name=' + encodeURIComponent(name) + '&message=' + encodeURIComponent(message);
            } else {
                body = '&email=' + encodeURIComponent(email) + '&name=' + encodeURIComponent(name) + '&phone=' + encodeURIComponent(phone);
            }

            xhr.open("POST", host + '/#/', true);
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            xhr.send(body);
            // ga.event({
            //     category: 'User',
            //     action: 'Order'
            // });
            this.setState({formSucces: true, name: '', email: '', phone: '', message: ''});
        } else {
            this.setState({firstTime: false});
        }
    }

    render() {
        const state = this.getPreparedData(this.state);
        if (this.props.isLoaded) {
            return <Loader/>;
        }
        const {fullForm, trainingPage, tvPage, aboutPage, mainForm, modal} = state;
        const validEmail = (validateEmail(this.state.email) && this.state.email !== '');
        const validForm = this.formValidator(!fullForm);
        if(fullForm) {
            return (
                <form onSubmit={this.handleSubmitFullForm} className={`form-mailing ${this.state.formSucces?'succes': ''}`}>
                    <div className="form-mailing__title">
                        {
                            modal ? (
                                'Связаться'
                            ) : (
                               'Начни свой путь прямо сейчас:'
                            )
                        }
                    </div>
                    <div className="form-mailing__inputs">
                        <FormInput
                            name='name'
                            type='text'
                            handleInputChange={this.handleInputChange}
                            error={!this.state.firstTime && this.state.name === '' && !this.state.formSucces}
                            placeholder={'Ваше имя'}
                            value={this.state.name}
                        />
                        <FormInput
                            name='email'
                            type='text'
                            handleInputChange={this.handleInputChange}
                            error={!this.state.firstTime && !validEmail && !this.state.formSucces}
                            placeholder={'Ваш e-mail'}
                            value={this.state.email}
                        />
                    </div>
                    <div className="form-mailing__inputs">
                        {
                            modal ? (
                                <FormInput
                                    name='message'
                                    type='text'
                                    handleInputChange={this.handleInputChange}
                                    error={!this.state.firstTime && this.state.message === '' && !this.state.formSucces}
                                    placeholder={'Сообщение'}
                                    value={this.state.message}
                                />
                            ) : (
                                <FormInput
                                    name='phone'
                                    type='number'
                                    handleInputChange={this.handleInputChange}
                                    error={!this.state.firstTime && this.state.phone === '' && !this.state.formSucces}
                                    placeholder={'Телефон'}
                                    value={this.state.phone}
                                />
                            )
                        }

                        <div className='form__group field field__btn'>
                            {
                                !trainingPage ? (
                                    <input type="submit" className={`btn`} value='Отправить заявку'/>
                                ) : (
                                    <button type="submit" className={window.innerWidth <= 1024 ? 'btn' : 'link-btn'}>Отправить заявку</button>
                                )
                            }
                            {/*<span className='error'>Заполните все поля!</span>*/}
                        </div>
                    </div>
                    <Rodal width={250} height={100} customStyles={{display: 'flex', alignItems: 'center', textAlign: 'center'}}
                           visible={this.state.formSucces} onClose={this.hide.bind(this)}>
                        <div className={'thanks'}>
                            Спасибо за заявку! Мы с Вами свяжемся!
                        </div>
                    </Rodal>
                </form>
            )
        }
        if(!fullForm) {
            return (
                <form onSubmit={this.handleSubscribeSubmit} className={`${aboutPage ? 'singup-form' : ''} form-mailing ${this.state.formSucces?'succes': ''}`}>
                    {
                        !aboutPage ? (
                            <div className="form-mailing__title">
                                Подпишись<br/> на нашу рассылку
                            </div>
                        ) : (
                            <div className="form-mailing__title singup-form__title">
                                <span className="singup-form__icon">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="11" height="12"
                                        viewBox="0 0 11 12" fill="none">
                                        <path d="M1.42139 10.5816V1.41846L9.86448 6.00144L1.42139 10.5816Z"
                                            stroke="#3BA9DC" strokeWidth="1.5" strokeMiterlimit="10"
                                            strokeLinecap="round" strokeLinejoin="round"/>
                                    </svg>
                                </span>
                                Отправьте форму за плюшки
                            </div>
                        )
                    }

                    {
                        tvPage && (
                            <div className="form-mailing__text">
                                Чтобы получать анонсы нового контента от Академии
                            </div>
                        )
                    }
                    {
                        aboutPage && (
                            <div className="singup-form__subtitle">
                                Изучение Книги Зоар требует от человека общих усилий желания и мысли (сердца и разума), чтобы соединить воедино все, что мы изучали прежде в науке каббала Когда мы читаем Книгу Зоар.
                            </div>
                        )
                    }
                    <div className={`form-mailing__inputs ${aboutPage ? 'singup-form__fields' : ''}`}>
                        <FormInput
                            name='name'
                            type='text'
                            handleInputChange={this.handleInputChange}
                            error={!this.state.firstTime && this.state.name === '' && !this.state.formSucces}
                            placeholder={'Ваше имя'}
                            value={this.state.name}
                            aboutPage
                        />
                        <FormInput
                            name='email'
                            type='text'
                            handleInputChange={this.handleInputChange}
                            error={!this.state.firstTime && !validEmail && !this.state.formSucces}
                            placeholder={'Ваш e-mail'}
                            value={this.state.email}
                            aboutPage
                        />
                    </div>
                    <div className="form-mailing__submit">
                        <div className='form__group field'>
                            <input type="submit" className={'btn'} value={`${aboutPage || mainForm ? 'запишись сейчас!' : 'Подпишись сейчас'}`}/>
                            {/*<span className='error'>Заполните все поля!</span>*/}
                        </div>
                        <div className='form-succes'>
                            <span>Спасибо за регистрацию! На e-mail, который Вы указали при регистрации, мы отправили линк активации Вашей учетной записи!</span>
                        </div>
                    </div>
                </form>
            )
        }
    }
}

export default FormRender;