import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import Layout from '../Layout';
import PhotosList from '../component/PhotosList';
import _get from 'lodash/get';
import EventsList from "../component/EventsList";
import {Seo} from "../component/Seo";

@inject('ui', 'photos', 'pages')
@withRouter
@observer
class TvsPage extends Component {
    componentDidMount() {
        this.props.pages.getById(328);
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        const page = _get(this.props, 'match.params.id', '');
        this.props.photos.getAll(category, page);
    };

    componentDidUpdate() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        const page = _get(this.props, 'match.params.id', '');
        this.props.photos.getAll(category, page);
    };

    goToCat = slug => {
        const url = slug ? `/gallery/${slug}` : '/gallery';
        this.props.history.push(url);
    };
    goNoCatToPage = id => {
        const url = id === 1 ? '/gallery' : `/gallery/page/${id}`;
        this.props.history.push(url);
    };

    goCatToPage = (slug, id) => {
        const url = id === 1 ? `/gallery/${slug}` : `/gallery/${slug}/page/${id}`;
        this.props.history.push(url);
    };
    render() {
        const {ui, photos: {photos, cats, isLoaded}, pages: {page}} = this.props;
        const category = _get(this.props, 'match.params.cat', '');
        const currentPage = _get(this.props, 'match.params.id', '');
        return (
            <Layout className="pageWrap">
                <Seo page={page} currentPage={currentPage} />
                <PhotosList
                    ui={ui}
                    photos={photos}
                    isLoaded={isLoaded}
                    category={category}
                    cats={cats}
                    goToCat={this.goToCat}
                    goNoCatToPage={this.goNoCatToPage}
                    goCatToPage={this.goCatToPage}
                />
            </Layout>
        );
    };
}

export default TvsPage;