import React, {Component} from 'react';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import ReactTooltip from 'react-tooltip';
import {limitWords} from "./limitWords";
import get from "lodash/get";

class PhotosListGallery extends Component {
    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
        }
    }

    onClickImgHandle = (index) => {
        this.setState({photoIndex: index, isOpen: true})
    };

    openLightBox = (isOpen, images, photoIndex) => {
        const {photos} = this.props;
        return isOpen && (
            <Lightbox
                mainSrc={images[photoIndex]}
                nextSrc={images[(photoIndex + 1) % images.length]}
                prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                onCloseRequest={() => this.setState({isOpen: false})}
                imageCaption={photos[photoIndex].title}
                animationDisabled={false}
                onMovePrevRequest={() =>
                    this.setState({
                        photoIndex: (photoIndex + images.length - 1) % images.length,
                    })
                }
                onMoveNextRequest={() =>
                    this.setState({
                        photoIndex: (photoIndex + 1) % images.length,
                    })
                }
            />
        )
    };

    render() {
        const {gallery, maxLength, photos, mainPage} = this.props;
        let images = [];
        Object.keys(gallery).map((src, index) => {
            if (images.length === maxLength) {
                return
            }
            images.push(gallery[index]);
        });
        let totalPhotos;
        if(photos){
            totalPhotos = get(photos, '[0].totalPosts')
        }
        const {photoIndex, isOpen} = this.state;
        if (images.length === 0) {
            return null;
        }
        if (!mainPage) {
            return (
                <>
                    {
                        images.map((src, index) => {
                            return (
                                <div className={'gallery-page__list--item'} key={index}>
                                    <div
                                        className="gallery-page__list--item-photo"
                                        style={{backgroundImage: `url(${src})`}}
                                        onClick={() => this.onClickImgHandle(index)}
                                    />
                                    <div className="gallery-page__list--item-description">
                                        {
                                            limitWords(photos[index].title, 17) !== photos[index].title ? (
                                                <>
                                                    <p data-tip={photos[index].title}>
                                                        {limitWords(photos[index].title, 17)}
                                                    </p>
                                                    <ReactTooltip place="bottom" type="info" effect="float"/>
                                                </>
                                            ) : (
                                                <p>
                                                    {photos[index].title}
                                                </p>
                                            )
                                        }
                                    </div>
                                </div>
                            );
                        })
                    }
                    {this.openLightBox(isOpen, images, photoIndex)}
                </>
            )
        } else {
            return (
                <>
                    <div className="kabbala-life">
                        <div className="kabbala-life__item first">
                            <span>Жизнь Академии каббалы:</span>
                            <a href="/#/gallery/">{totalPhotos} фото в архиве</a>
                        </div>
                        {
                            images.map((src, index) => (
                                <div className="kabbala-life__item" key={index}
                                     style={{backgroundImage: `url(${src})`}}
                                     onClick={() => this.onClickImgHandle(index)}
                                />
                            ))
                        }
                    </div>
                    {this.openLightBox(isOpen, images, photoIndex)}
                </>
            )
        }

    }
}

export default PhotosListGallery;