import React from 'react';
import {observer} from "mobx-react";
import pcidss from '../img/pci_dss.png'
import Loader from "./Loader";
import get from 'lodash/get'
import {splitString} from "./SplitString";
import YouTube from "react-youtube";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Donate = ({isLoaded, donate}) => {
    const youtubeId = donate.youtubeId;
    const soonVebinar = () => {
        const opts = {
            height: '100%',
            width: '100%',
            playerVars: {
                autoplay: 0
            }
        };
        return (
            <YouTube
                opts={opts}
                videoId={youtubeId}
                containerClassName={'donate-page__video'}
            />
        );
    };
    const reactWidth = window.innerWidth;
    const mobile = (reactWidth < 1024);
    let leftTop = get(donate.left, '0');
    let leftContact = get(donate.left, '1');
    let button1 = get(donate.right, '0.buttons[0]');
    let button2 = get(donate.right, '0.buttons[1]');
    let projects = get(donate.right, '0.projects');
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <div className="page training-page donate-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-content fw">
                                <div className="page-content__wrap donate">
                                    <div className="donate__columns">
                                        <div className="sidebar">
                                            <div className="donate-title page-content__title">
                                                <h1>{donate.title}</h1>
                                            </div>
                                            {
                                                leftTop && (
                                                    <div className="donate__description donate-description">
                                                        <div className="donate-description__title">
                                                            {leftTop.title}
                                                        </div>
                                                        <div className="donate-description__text"
                                                             dangerouslySetInnerHTML={{__html: leftTop.text}}/>
                                                    </div>
                                                )
                                            }
                                            {
                                                !mobile && leftContact && (
                                                    <>
                                                        <div className="payment-info">
                                                            <div className="payment-info__image">
                                                                <LazyLoadImage
                                                                    alt={"БЕЗОПАСНОСТЬ ОНЛАЙН ОПЛАТЫ"}
                                                                    effect="blur"
                                                                    src={pcidss}
                                                                />
                                                            </div>
                                                            <div className="payment-info__description">
                                                                <div className="payment-info__title">
                                                                    БЕЗОПАСНОСТЬ ОНЛАЙН ОПЛАТЫ
                                                                </div>
                                                                <div className="payment-info__text">
                                                                    <p>Наша форма пожертвования полностью соответствует
                                                                        PCI,
                                                                        высочайшему международному стандарту защиты
                                                                        данных
                                                                        вашей платежной карты.</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="contact-block">
                                                            <div className="contact-block__title">
                                                                {leftContact.title}
                                                            </div>
                                                            <div className="contact-block__text"
                                                                 dangerouslySetInnerHTML={{__html: leftContact.text}}/>
                                                        </div>
                                                    </>
                                                )
                                            }
                                        </div>
                                        <div className="main-content">
                                            <div className="donate-video">
                                                {youtubeId && soonVebinar()}
                                                {
                                                    button1 && Object.keys(button1).map((key, index) => (
                                                        index === 0 && <div className="donate-btn-wrap" key={index}>
                                                            <button onClick={() => window.open(button1['url'], '_blank')}
                                                                    className="btn btn-donate">{button1['text']}</button>
                                                        </div>
                                                    ))
                                                }
                                            </div>
                                            <div className="donate-projects">
                                                {
                                                    projects && Object.keys(projects).map((key, index) => (
                                                        <div className="donate-projects__item donate-project"
                                                             key={index}>
                                                            <div className="donate-project__title">
                                                                <a href={projects[key].url} target="_blank" className="clrs-under"
                                                                   dangerouslySetInnerHTML={{__html: splitString(projects[key].name)}}/>
                                                            </div>
                                                            <div className="donate-project__subtitle">
                                                                {projects[key].title}
                                                            </div>
                                                            <div className="donate-project__text"
                                                                 dangerouslySetInnerHTML={{__html: projects[key].description}}/>
                                                            <div className="donate-project__btn-wrap">
                                                                <button
                                                                    onClick={() => window.open(projects[key].btnUrl, '_blank')}
                                                                    className="btn btn-donate donate-project-btn">
                                                                    помочь проекту
                                                                </button>
                                                            </div>
                                                        </div>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    </div>
                                    {
                                        mobile && leftContact && <div className="sidebar-mobile">
                                            <div className="payment-info">
                                                <div className="payment-info__image">
                                                    <LazyLoadImage
                                                        alt={"БЕЗОПАСНОСТЬ ОНЛАЙН ОПЛАТЫ"}
                                                        effect="blur"
                                                        src={pcidss}
                                                    />
                                                </div>

                                                <div className="payment-info__description">
                                                    <div className="payment-info__title">
                                                        БЕЗОПАСНОСТЬ ОНЛАЙН ОПЛАТЫ
                                                    </div>

                                                    <div className="payment-info__text">
                                                        <p>Наша форма пожертвования полностью соответствует PCI,
                                                            высочайшему
                                                            международному стандарту защиты данных вашей платежной
                                                            карты.</p>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="contact-block">
                                                <div className="contact-block__title">
                                                    {leftContact.title}
                                                </div>
                                                <div className="contact-block__text"
                                                     dangerouslySetInnerHTML={{__html: leftContact.text}}/>
                                                {
                                                    button1 && Object.keys(button1).map((key, index) => (
                                                        index === 0 && <div className="donate-btn-wrap" key={index}>
                                                            <button onClick={() => window.open(button1['url'], '_blank')}
                                                                    className="btn btn-donate">{button1['text']}</button>
                                                        </div>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(Donate);