import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Main {
    @observable isLoaded = true;
    @observable main = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.General.get()
                .then(action(({main}) => {
                    this.main = main || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Main();