import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import iconEventDate from '../img/event-date-icon.svg'
import EventListGallery from "./EventListGallery";
import {Pagination} from "./Pagination";
import Loader from "./Loader";
import get from 'lodash/get';
import {eventSidebarBlock} from "./TvsList";
import {LazyLoadImage} from "react-lazy-load-image-component";

const EventsList = ({events, isLoaded, cats, category, goToCat, goCatToPage, goNoCatToPage, vebinar}) => {
    const reactWidth = window.innerWidth;
    let totalPages;
    let currentPage;
    if(events.length > 0) {
        totalPages = get(events, '[0].totalPages');
        currentPage = get(events, '[0].currentPage');
    }
    const renderFixedInner = (isLoaded) => {
        if (!isLoaded) {
            return <div>Loader</div>;
        }
        return (
            <>
                {events.map(event =>
                    event.fixed && (
                        <div className="events-page__fixed" key={event.id}>
                            <div className="events-page__fixed--img">
                                <div className="fix-image" style={{backgroundImage: `url(${event['prevImg']})`}} >
                                    <LazyLoadImage
                                        alt={event['title']}
                                        effect="blur"
                                        src={event['prevImg']}
                                        style={{opacity: 0}}
                                    />
                                </div>
                            </div>
                            <div className="events-page__fixed--eventDate">
                                <img src={iconEventDate}
                                     alt=""/><span>{!event['eventDate'] ? 'Завершено' : event['eventDate']}</span>
                            </div>
                            <div className="events-page__fixed--title">
                                <Link to={`/events/${event.id}`}>{event['title']}</Link>
                            </div>
                            <div className="events-page__fixed--content"
                                 dangerouslySetInnerHTML={{__html: event['excerpt']}}/>
                        </div>
                    )
                )}
            </>
        )
    };
    const renderInner = () => {
        return (
            <>
                {events.map(event =>
                    !event.fixed && (
                        <div className={'events-page__list--item'} key={event.id}>
                            <div className="events-page__list--image">
                                <Link to={`/events/${event.id}`}>
                                    <LazyLoadImage
                                        alt={event['title']}
                                        effect="blur"
                                        src={event['prevImg']}
                                    />
                                </Link>
                            </div>
                            <div className="events-page__list--content">
                                <div className="mob-ev">
                                    <div className="events-page__list--eventDate">
                                        <span>{!event['eventDate'] ? 'Завершено' : event['eventDate']}</span>
                                    </div>
                                    <div className="events-page__list--title">
                                        <Link to={`/events/${event.id}`} >{event['title']}</Link>
                                    </div>
                                </div>
                                <div className="events-page__list--text"
                                     dangerouslySetInnerHTML={{__html: event['excerpt']}}/>
                                {event.gallery && window.innerWidth > 1024 &&
                                <EventListGallery gallery={event.gallery} linkTo={`/events/${event.id}`} maxLength={5}/>}
                            </div>
                        </div>
                    )
                )}
            </>
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                {
                    reactWidth > 1024 && (
                        <style dangerouslySetInnerHTML={{
                            __html: `
            .filters-list::after {width: ${reactWidth}px }
            .page-content::after {width: ${reactWidth}px }
             `
                        }}/>
                    )
                }
                {
                    reactWidth <= 1024 && (
                        <style dangerouslySetInnerHTML={{
                            __html: `
            .events-page__fixed--img, .events-page__fixed--img::after,
             .page-sidebar::after, .fix-image {width: ${reactWidth}px }
             `
                        }}/>
                    )
                }
                <style
                    dangerouslySetInnerHTML={{
                        __html:`
                    .events-page__list--item::before,.events-page__list--item::after { width: ${reactWidth}px }`
                    }}
                />
                <div className="page events-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                {cats.length > 1 && (
                                    <div className="filters-list">
                            <span className={`filters-list__item ${!category ? 'filters-list__item_active' : ''}`}
                                  onClick={() => goToCat()}>Все события</span>
                                        {cats.map(cat => (
                                            <span key={cat.slug}
                                                  className={`filters-list__item ${cat.id === category ? 'filters-list__item_active' : ''}`}
                                                  onClick={() => goToCat(cat.id)}>{cat.name}</span>
                                        ))}
                                    </div>
                                )}
                                {
                                    window.innerWidth > 1024 ? eventSidebarBlock(vebinar, false) : null
                                }
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        {(!category) ?
                                            <h1>Все события</h1>
                                            :
                                            cats.map(cat => (
                                                (cat.id === category || !category) ?
                                                    <h1 key={cat.slug}>{cat.id === category && cat.name}</h1>
                                                    : null
                                            ))
                                        }
                                    </div>
                                    {renderFixedInner(isLoaded)}
                                    <div className="events-page__list">
                                        {renderInner(isLoaded)}
                                    </div>
                                    <Pagination
                                        totalPages={totalPages}
                                        goNoCatToPage={goNoCatToPage}
                                        goCatToPage={goCatToPage}
                                        currentPage={currentPage}
                                        category={category}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(EventsList);