import React from 'react';
import {observer} from "mobx-react";
import EventListGallery from "./EventListGallery";
import Loader from "./Loader";
import {eventSidebarBlock} from "./TvsList";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Event = ({event, isLoaded, cats, category, goToCat, redneredContent}) => {
        const renderEvent = () => {
            return event && (
                <>
                    <div className="page-content__title">
                        <h1>{event.title}</h1>
                    </div>
                    <div className="events-page__content">
                        {event.img && (
                            <LazyLoadImage
                                alt={event.title}
                                effect="blur"
                                src={event.img}
                                className={"post__main-image"}
                            />
                        )}
                        <div className="post__content-inner" dangerouslySetInnerHTML={{__html: redneredContent}}/>
                        {event.gallery &&
                        <EventListGallery gallery={event.gallery} maxLength={7}/>}
                    </div>
                </>
            )
        };
        if (!isLoaded) {
            return <Loader/>;
        } else {
            return (
                <>
                    <div className="page events-page">
                        <div className="container">
                            <div className="page-wrapper">
                                <div className="page-sidebar">
                                    {cats.length > 1 && (
                                        <div className="filters-list">
                                    <span className={`filters-list__item`}
                                          onClick={() => goToCat()}>Все события</span>
                                            {cats.map(cat => (
                                                <span key={cat.slug}
                                                      className={`filters-list__item`}
                                                      onClick={() => goToCat(cat.id)}>{cat.name}</span>
                                            ))}
                                        </div>
                                    )}
                                    {
                                        window.innerWidth > 1024 ? eventSidebarBlock() : ''
                                    }
                                </div>
                                <div className="page-content">
                                    <div className="page-content__wrap">
                                        {renderEvent()}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            );
        }
    }
;

export default observer(Event);