import React from 'react';
import {observer} from "mobx-react";
import Loader from "./Loader";
import {eventSidebarBlock} from "./TvsList";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Info = ({info, isLoaded, cats, category, goToCat, redneredContent}) => {
        const renderEvent = () => {
            return info && (
                <>
                    <div className="page-content__title">
                        <h1>{info.title}</h1>
                    </div>
                    <div className="events-page__content">
                        <div className="post__content-inner" dangerouslySetInnerHTML={{__html: redneredContent}}/>
                    </div>
                </>
            )
        };
        if (!isLoaded) {
            return <Loader/>;
        } else {
            return (
                <>
                    <div className="page events-page">
                        <div className="container">
                            <div className="page-wrapper">
                                <div className="page-sidebar">
                                    {cats.length > 1 && (
                                        <div className="filters-list">
                                            {cats.slice().reverse().map(cat => (
                                                <span key={cat.slug}
                                                      className={`filters-list__item`}
                                                      onClick={() => goToCat(cat.id)}>{cat.name}</span>
                                            ))}
                                        </div>
                                    )}
                                    {
                                        window.innerWidth > 1024 ? eventSidebarBlock() : ''
                                    }
                                </div>
                                <div className="page-content">
                                    <div className="page-content__wrap">
                                        {renderEvent()}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            );
        }
    }
;

export default observer(Info);