import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Teachers {
    @observable isLoaded = true;
    @observable teachers = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Teachers.getAll()
                .then(action(({teachers}) => {
                    this.teachers = teachers || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Teachers();