import React, {useEffect, useRef, useState} from 'react';
import {observer} from "mobx-react";
import PhotosListGallery from "./PhotosListGallery";
import {Pagination} from "./Pagination";
import get from "lodash/get";
import Loader from "./Loader";

const PhotosList = ({ui, photos, isLoaded, cats, category, goToCat, goCatToPage, goNoCatToPage}) => {
    let totalPages;
    let currentPage;
    if (photos.length > 0) {
        totalPages = get(photos, '[0].totalPages');
        currentPage = get(photos, '[0].currentPage');
    }
    const wrapperRef = useRef(null);
    const [active, setActive] = useState(false);
    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);
    const handleClickOutside = (event) => {
        if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
            setActive(false)
        }
    };
    const years = cats.slice().sort();
    let gallery = {};
    photos.map((photo, index) => {
        gallery[index] = photo['img'];
    });
    const {options} = ui;
    const dropDownYears = (years) => {
        return (
            <div ref={wrapperRef} className={`filters-list__select`}>
                <button className={`drop-arrow ${active ? 'active' : ''}`} onClick={() => setActive(!active)}>
                    <svg width="9" height="8" viewBox="0 0 9 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M7.36112 0.75L4.33003 6L1.29894 0.749999L7.36112 0.75Z" stroke="#929292"
                              strokeWidth="1.5"/>
                    </svg>
                </button>
                <div className={`filters-list__drop ${active ? 'open' : ''}`}>
                    {years.map((year, index) => (
                        index > 2 && <span key={year.slug}
                                           className={`filters-list__item ${year.id === category ? 'filters-list__item_active' : ''}`}
                                           onClick={() => goToCat(year.id)} style={{marginRight: 0}}>{year.name}</span>
                    ))}
                </div>
            </div>
        )
    };
    const renderTopContent = (isLoaded) => {
        // if (!isLoaded) {
        //     return <div>Loader</div>;
        // }
        return (
            <>
                <div className="page-content__title">
                    <h1>Фотогалерея</h1>
                </div>
                <div className="page-content__cats">
                    {cats.length > 1 && (
                        <div className="filters-list">
                            <span className={`filters-list__item ${!category ? 'filters-list__item_active' : ''}`}
                                  onClick={() => goToCat()}>Все</span>
                            {
                                window.innerWidth > 1024 ? (
                                    <>
                                        {
                                            years.map((year, index) => (
                                                index <= 2 && (
                                                    <span key={index}
                                                          className={`filters-list__item ${year.id === category ? 'filters-list__item_active' : ''}`}
                                                          onClick={() => goToCat(year.id)}>{year.name}</span>
                                                )
                                            ))
                                        }
                                        {dropDownYears(years)}
                                    </>
                                ) : (
                                    years.map((year, index) => (
                                        <span key={index}
                                              className={`filters-list__item ${year.id === category ? 'filters-list__item_active' : ''}`}
                                              onClick={() => goToCat(year.id)}>{year.name}</span>
                                    ))
                                )
                            }
                        </div>
                    )}
                </div>
            </>
        )
    };
    const renderInner = isLoaded => {
        if (!isLoaded) {
            return <Loader/>;
        }
        return (
            <>
                {
                    <PhotosListGallery gallery={gallery} photos={photos}/>
                }
            </>
        )
    };

    return (
        <>
            <div className="page gallery-page">
                <div className="container">
                    <div className="page-wrapper">
                        <div className="page-content fw">
                            <div className="page-content__wrap">
                                <div className="page-content__top">
                                    {renderTopContent(isLoaded)}
                                </div>
                                <div className="gallery-page__list">
                                    {renderInner(isLoaded)}
                                </div>
                            </div>
                        </div>
                    </div>
                    <Pagination
                        totalPages={totalPages}
                        goNoCatToPage={goNoCatToPage}
                        goCatToPage={goCatToPage}
                        currentPage={currentPage}
                        category={category}
                    />
                </div>
            </div>
        </>
    );
};

export default observer(PhotosList);