import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import {Seo} from "../component/Seo";
import Info from "../component/Info";
import get from 'lodash/get';

@inject('ui', 'infos', 'pages')
@observer
class EventPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const {match: {params: {id}}} = this.props;
        this.props.infos.setInfoId(id);
        this.props.infos.getInfo(id);
        this.props.pages.getInfosSeo(id);
    }

    goToCat = slug => {
        const url = slug ? `/info/category/${slug}` : '/info';
        this.props.history.push(url);
    };

    render() {
        const {ui, infos: {isLoaded, cats, category}, pages: {page}} = this.props;
        const info = this.props.infos.info;
        let redneredContent;
        if(page) {
            redneredContent = get(page, 'content.rendered')
        }
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Info info={info} redneredContent={redneredContent} cats={cats} category={category} isLoaded={isLoaded} goToCat={this.goToCat}/>
            </Layout>
        );
    }
}

export default EventPage;