import {observable, action} from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Pages {
    @observable isLoaded = true;
    @observable page = {};
    @observable loadedId = null;

    @action getById(id) {
        if (id !== this.loadedId){
            this.isLoaded = false;
            return agent.Pages.getById(id)
                .then(action((page) => {
                    this.loadedId = id;
                    this.page = page || {};
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => { this.isLoaded = true; }));
        }
    }
    @action getEventSeo(id) {
        if (id !== this.loadedId){
            this.isLoaded = false;
            return agent.Pages.getEventSeo(id)
                .then(action((page) => {
                    this.loadedId = id;
                    this.page = page || {};
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => { this.isLoaded = true; }));
        }
    }
    @action getInfoSeo(id) {
        if (id !== this.loadedId){
            this.isLoaded = false;
            return agent.Pages.getInfoSeo(id)
                .then(action((page) => {
                    this.loadedId = id;
                    this.page = page || {};
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => { this.isLoaded = true; }));
        }
    }
    @action getInfosSeo(id) {
        if (id !== this.loadedId){
            this.isLoaded = false;
            return agent.Pages.getInfosSeo(id)
                .then(action((page) => {
                    this.loadedId = id;
                    this.page = page || {};
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => { this.isLoaded = true; }));
        }
    }
}

export default new Pages();