import React, {Component} from 'react';
import Loader from './Loader';
import _get from 'lodash/get';
import {Seo} from "./Seo";

class Page extends Component {
    render() {
        const {page = {}, isLoaded} = this.props;
        const title = _get(page, 'title.rendered', '');
        const contentHTML = _get(page, 'content.rendered', '');

        if (!isLoaded) {
            return <Loader/>
        } else {
            return (
                <>
                    <Seo page={page}/>
                    <div className="page training-page">
                        <div className="container">
                            <div className="page-content fw">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{title}</h1>
                                    </div>
                                    <div className="post__content-inner"
                                         dangerouslySetInnerHTML={{__html: contentHTML}}/>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            );
        }
    }
}

export default Page;