import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import Loader from "./Loader";
import FormRender from "./Forms";
import {LazyLoadImage} from "react-lazy-load-image-component";

const Training = ({training, isLoaded}) => {
    const renderInner = () => {
        return (
            training.map(train => (
                <div className="training-page__content" key={train.title + Math.random()}>
                    {
                        train.topBanner && (
                            <div className="training-page__topbanner">
                                <div className="training-image">
                                    <LazyLoadImage
                                        alt={train.topBanner.imgText}
                                        effect="blur"
                                        src={train.topBanner.img}
                                    />
                                </div>
                                <p>{train.topBanner.imgText}</p>
                            </div>
                        )
                    }
                    <div className="training-page__columns">
                        {
                            train.leftTopText && (
                                <div className="training-page__columns--item" dangerouslySetInnerHTML={{__html: train.leftTopText}} />
                            )
                        }
                        {
                            train.filialy && (
                                <div className="training-page__columns--item filialy">
                                    <p className="filialy-title">
                                        Вы сможете выбрать наиболее подходящий вам филиал для очного обучения:
                                    </p>
                                    <div dangerouslySetInnerHTML={{__html: train.filialy}}/>
                                </div>
                            )
                        }
                        {
                            train.bottomBanners && (
                                train.bottomBanners.map(item => (
                                    <div className="training-page__columns--item" key={item.img + Math.random()}>
                                        <div className="training-page__columns--item-img">
                                            <div className="training-image">
                                                <LazyLoadImage
                                                    alt={item.imgText}
                                                    effect="blur"
                                                    src={item.img}
                                                />
                                            </div>
                                            <p>{item.imgText}</p>
                                        </div>
                                        <div dangerouslySetInnerHTML={{__html: item.text}} />
                                    </div>
                                ))
                            )
                        }
                    </div>
                </div>
            ))
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <div className="page training-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-content fw">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{training.map(item => item.title)}</h1>
                                    </div>
                                    {renderInner()}
                                    <div className="page-content__form">
                                        <FormRender trainingPage={true} fullForm={true} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(Training);