import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import {Pagination} from "./Pagination";
import Loader from "./Loader";
import get from 'lodash/get';
import {eventSidebarBlock} from "./TvsList";
import {LazyLoadImage} from "react-lazy-load-image-component";

const InfosList = ({infos, isLoaded, cats, category, goToCat, goCatToPage, goNoCatToPage, vebinar}) => {
    const reactWidth = window.innerWidth;
    let totalPages;
    let currentPage;
    if(infos.length > 0) {
        totalPages = get(infos, '[0].totalPages');
        currentPage = get(infos, '[0].currentPage');
    }
    const renderInner = () => {
        return (
            <>
                {infos.map(info =>
                        <div className={'events-page__list--item'} key={info.id}>
                            <div className="events-page__list--image">
                                <Link to={`/info/${info.id}`}>
                                    <LazyLoadImage
                                        alt={info['title']}
                                        effect="blur"
                                        src={info['prevImg']}
                                    />
                                </Link>
                            </div>
                            <div className="events-page__list--content">
                                <div className="mob-ev">
                                    <div className="events-page__list--title">
                                        <Link to={`/info/${info.id}`} >{info['title']}</Link>
                                    </div>
                                </div>
                                <div className="events-page__list--text"
                                     dangerouslySetInnerHTML={{__html: info['excerpt']}}/>
                            </div>
                        </div>
                )}
            </>
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                {
                    reactWidth > 1024 && (
                        <style dangerouslySetInnerHTML={{
                            __html: `
            .filters-list::after {width: ${reactWidth}px }
            .page-content::after {width: ${reactWidth}px }
             `
                        }}/>
                    )
                }
                {
                    reactWidth <= 1024 && (
                        <style dangerouslySetInnerHTML={{
                            __html: `
            .events-page__fixed--img, .events-page__fixed--img::after,
             .page-sidebar::after, .fix-image {width: ${reactWidth}px }
             `
                        }}/>
                    )
                }
                <style
                    dangerouslySetInnerHTML={{
                        __html:`
                    .events-page__list--item::before,.events-page__list--item::after { width: ${reactWidth}px }`
                    }}
                />
                <div className="page events-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                {cats.length > 1 && (
                                    <div className="filters-list">
                                        {cats.slice().reverse().map(cat => (
                                            <span key={cat.slug}
                                                  className={`filters-list__item ${cat.id === category ? 'filters-list__item_active' : ''}`}
                                                  onClick={() => goToCat(cat.id)}>{cat.name}</span>
                                        ))}
                                    </div>
                                )}
                                {
                                    window.innerWidth > 1024 ? eventSidebarBlock(vebinar, false) : null
                                }
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        {cats.map(cat => (
                                                (cat.id === category || !category) ?
                                                    <h1 key={cat.slug}>{cat.id === category && cat.name}</h1>
                                                    : null
                                            ))
                                        }
                                    </div>
                                    <div className="events-page__list">
                                        {renderInner(isLoaded)}
                                    </div>
                                    <Pagination
                                        totalPages={totalPages}
                                        goNoCatToPage={goNoCatToPage}
                                        goCatToPage={goCatToPage}
                                        currentPage={currentPage}
                                        category={category}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(InfosList);