import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import Books from "../component/Books";
import FormRender from "../component/Forms";
import {Seo} from "../component/Seo";

@inject('ui', 'books', 'pages')
@observer
class BooksPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.books.getAll();
        this.props.pages.getById(140);
    }

    render() {
        const {ui, books: {books, isLoaded}, pages: {page}} = this.props;
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Books books={books} isLoaded={isLoaded}/>
                <div className="bottom-form bottom-form-books">
                    <div className="container">
                        <div className="bottom-form__content">
                            <FormRender  />
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

export default BooksPage;