import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Library {
    @observable isLoaded = true;
    @observable library = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.Library.getAll()
                .then(action(({library}) => {
                    this.library = library || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Library();