import React, { Component } from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import {Seo} from "../component/Seo";
import Laytman from "../component/Laytman";
import get from 'lodash/get'

@inject('ui', 'pages')
@observer
class ContactsPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.pages.getById(383);
    }
    toggleMenu = open => {
        const {ui: {openMenu}} = this.props;
        const status = typeof open === 'undefined' ? !openMenu :  open;

        this.props.ui.toggleMenu(status)
    };
    isActive = page => {
        const path = this.props.match.path;
        return path.indexOf(page) !== -1 ? 'filters-list__item_active' : '';
    };
    handleClick = () => {
        this.toggleMenu(false);
    };
    render() {
        const { ui, pages: {page, isLoaded}} = this.props;
        let title;
        let content;
        if(page) {
            title = get(page, 'title.rendered');
            content = get(page, 'content.rendered');
        }
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Laytman id={page.id} title={title} content={content} isLoaded={isLoaded} isActive={this.isActive} handleClick={this.handleClick} />
            </Layout>
        );
    }
}

export default ContactsPage;