import { observable, action, computed } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class About {
    @observable isLoaded = true;
    @observable about = [];

    @action getAll() {
        if (this.isLoaded) {
            this.isLoaded = false;
            return agent.About.getAll()
                .then(action(({about}) => {
                    this.about = about || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new About();