import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import Layout from '../Layout';
import TvsList from '../component/TvsList';
import _get from 'lodash/get';
import {Seo} from "../component/Seo";

@inject('ui', 'tvs', 'pages')
@withRouter
@observer
class TvsPage extends Component {
    componentDidMount() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        // if(!category) {
            this.props.pages.getById(326);
        // } else {
        //     this.props.pages.getById();
        // }
        this.props.tvs.getAll(category);
    };

    componentDidUpdate() {
        // document.body.scrollTop = 0;
        // document.documentElement.scrollTop = 0;
        const category = _get(this.props, 'match.params.cat', '');
        this.props.tvs.getAll(category);
        // if(!category) {
        //     this.props.pages.getById(326);
        // } else {
        //     this.props.pages.getById();
        // }
    };

    // goToCat = slug => {
    //     const url = slug ? `/tv/category/${slug}` : '/tv';
    //     this.props.history.push(url);
    // };

    toggleMenu = open => {
        const {ui: {openMenu}} = this.props;
        const status = typeof open === 'undefined' ? !openMenu :  open;

        this.props.ui.toggleMenu(status)
    };
    isActive = page => {
        const path = this.props.match.path;
        return path.indexOf(page) !== -1 ? 'filters-list__item_active' : '';
    };
    handleClick = () => {
        this.toggleMenu(false);
    };

    render() {
        const {ui, tvs: {tvs, cats, isLoaded, nextVebinar}, pages: {page}} = this.props;
        let vebinar;
        if(tvs.length > 0) {
            vebinar = _get(nextVebinar, '[0]')
        }
        const category = _get(this.props, 'match.params.cat', '');
        let catTitle;
        cats.map(cat => (
            (cat.id === category || !category) ?
                cat.id === category ? (catTitle = cat.name) : null
                : null
        ));
        return (
            <Layout className="pageWrap">
                <Seo page={page} catTitle={catTitle} />
                <TvsList
                    ui={ui}
                    tvs={tvs}
                    isLoaded={isLoaded}
                    category={category}
                    cats={cats}
                    isActive={this.isActive}
                    handleClick={this.handleClick}
                    // goToCat={this.goToCat}
                    vebinar={vebinar}
                />
            </Layout>
        );
    };
}

export default TvsPage;