import React, { Component } from 'react';
import {inject, observer} from "mobx-react";
import Layout from '../Layout';
import Teachers from "../component/Teachers";
import FormRender from "../component/Forms";
import {Seo} from "../component/Seo";

@inject('ui', 'teachers', 'pages')
@observer
class TeachersPage extends Component {
    componentDidMount() {
        this.props.pages.getById(76);
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.props.teachers.getAll();
    }
    toggleMenu = open => {
        const {ui: {openMenu}} = this.props;
        const status = typeof open === 'undefined' ? !openMenu :  open;

        this.props.ui.toggleMenu(status)
    };
    isActive = page => {
        const path = this.props.match.path;
        return path.indexOf(page) !== -1 ? 'filters-list__item_active' : '';
    };
    handleClick = () => {
        this.toggleMenu(false);
    };
    render() {
        const { ui, teachers: { teachers, isLoaded }, pages: {page}} = this.props;
        return (
            <Layout className="pageWrap">
                <Seo page={page} />
                <Teachers teachers={teachers} isLoaded={isLoaded} isActive={this.isActive} handleClick={this.handleClick} />
                <div className="bottom-form">
                    <div className="container">
                        <div className="bottom-form__content">
                            <FormRender  />
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

export default TeachersPage;