import React from 'react';
import {observer} from "mobx-react";
import {Link} from "react-router-dom";
import EventListGallery from "./EventListGallery";
import Loader from "./Loader";
import FormRender from "./Forms";

const About = ({about, isLoaded, isActive, handleClick, redneredContent}) => {
    const renderInner = () => {
        return (
            about.map((item) => (
                <div className="teachers-page__content" key={item.title}>
                    <div className="post__content-inner" dangerouslySetInnerHTML={{__html: redneredContent}}/>
                    {item.gallery &&
                    <EventListGallery gallery={item.gallery} maxLength={7}/>}

                    {/* Form Только на Desktop */}
                    {/*    <div className="singup-form-wrap">*/}
                    {/*        <FormRender aboutPage />*/}
                    {/*    </div>*/}
                    {/* /Form */}
                </div>
            ))
        )
    };
    if (!isLoaded) {
        return <Loader/>;
    } else {
        return (
            <>
                <div className="page teachers-page">
                    <div className="container">
                        <div className="page-wrapper">
                            <div className="page-sidebar">
                                <div className="filters-list">
                                    <Link to='/about' onClick={handleClick}
                                          className={`filters-list__item ${isActive('about')}`}>Об ассоциации</Link>
                                    <Link to='/o-mihaele-lajtmane' onClick={handleClick}
                                          className={`filters-list__item ${isActive('o-mihaele-lajtmane')}`}>О Михаэле Лайтмане</Link>
                                    <Link to='/projects' onClick={handleClick}
                                          className={`filters-list__item ${isActive('projects')}`}>Проекты академии</Link>
                                    <Link to='/teachers' onClick={handleClick}
                                          className={`filters-list__item ${isActive('teachers')}`}>Преподаватели</Link>
                                    <Link to='/contacts' onClick={handleClick}
                                          className={`filters-list__item ${isActive('contacts')}`}>Контакты</Link>
                                </div>
                            </div>
                            <div className="page-content">
                                <div className="page-content__wrap">
                                    <div className="page-content__title">
                                        <h1>{about.map(item => item.title)}</h1>
                                    </div>
                                    {renderInner(isLoaded)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
};

export default observer(About);