import { observable, action } from 'mobx';
import agent from '../agent';
import ui from './ui';

export class Events {
    @observable isLoaded = true;
    @observable events = [];
    @observable event = [];
    @observable eventId = 0;
    @observable cats = [];
    @observable nextVebinar = [];
    @observable lastCategory = null;
    @observable lastPage = null;

    @action setEventId(id){
        this.eventId = id;
    }

    @action getAll(category, page) {
        if (category !== this.lastCategory && this.isLoaded || page !== this.lastPage && this.isLoaded) {
            this.isLoaded = false;
            this.lastCategory = category;
            this.lastPage = page;
            return agent.Events.getAll(category, page)
                .then(action(({events, cats, nextVebinar}) => {
                    this.events = events || [];
                    this.cats = cats || [];
                    this.nextVebinar = nextVebinar || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
    @action getEvent(id) {
        if(id && this.isLoaded) {
            this.isLoaded = false;
            return agent.Events.getId(id)
                .then(action(({event, cats}) => {
                    this.event = event || [];
                    this.cats = cats || [];
                    this.isLoaded = true;
                }))
                .catch(action(err => {
                    ui.setError(err);
                    throw err;
                }))
                .finally(action(() => {
                    this.isLoaded = true;
                }));
        }
    }
}

export default new Events();